.cl {
    &--white {
        color: $white;
        @include heading {
            color: $white; } }
    &--silver {
        color: $silver;
        @include heading {
            color: $silver; } }
    &--blue {
        color: $blue;
        @include heading {
            color: $blue; } }
    &--darkBlue {
        color: $blue;
        @include heading {
            color: $blue; } } }
